@import url(https://fonts.googleapis.com/icon?family=Material+Icons&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli:400,600,700,900&display=swap);
* {
	max-width: 100%;
	max-height: 100%;
	box-sizing: border-box;
}

body {
	font-size: calc(16px + 10 * ((100vw - 320px) / (1920 - 320)));
	line-height: 1.2em;

	/* line-height: calc(1.6em + 0.4 * ((100vw - 320px) / (1920 - 320))); */
	margin: 0;
	padding: 0;
	font-family: 'Poppins', 'Roboto', 'PT Sans', 'Muli', -apple-system,
		BlinkMacSystemFont, 'Segoe UI', 'Noto Sans', 'Oxygen', 'Ubuntu', 'Cantarell',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
/* @import url('https://fonts.googleapis.com/css?family=Mukta:300,400,600,700&display=swap'); */
/* @import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,600,700,900&display=swap'); */
/* @import url('https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap'); */
/* @import url('https://fonts.googleapis.com/css?family=Gothic+A1:200,300,400,500,600,700,900&display=swap'); */

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

a,
a:visited {
	color: inherit;
	text-decoration: none;
}

audio,
canvas,
progress,
video {
	display: inline-block;
}

img {
	max-width: 100%;
}

button {
	border-style: none;
	cursor: pointer;
	box-shadow: 0 2px 6px rgba(20, 20, 20, 0.16);
}

button:hover {
	box-shadow: 4px 4px 8px rgba(20, 20, 20, 0.16);
}

audio:not([controls]) {
	display: none;
	height: 0;
}

ul,
ol,
li {
	list-style: none;
	padding: none;
}

legend {
	background-color: #000;
	color: #fff;
	padding: 3px 6px;
}

::-webkit-scrollbar {
	width: 8px;
}

::-webkit-scrollbar-thumb {
	background: royalblue; /* linear-gradient(#16df84, #162432); */
	border-radius: 2px;
}

.bg_WhiteSmoke {
	background-color: #f5f5f5;
}

.debugger {
	background-color: antiquewhite;
	width: 100%;
	margin: 4vh auto;
	padding: 1rem;
	text-align: start;
}

.external_Link {
	color: salmon;
}

.material_icons {
	font-size: inherit;
	color: rebeccapurple;
}

.post:hover {
	background: #16df8480;
}

.highlight {
	padding: 0 2px 0 2px;
	border-bottom: 2px solid #16df84;
	box-shadow: inset 0 -3px 0 #16df84;
}

@media only screen and (min-width: 481px) {
	.debugger {
		width: 80%;
		padding: 2rem;
		text-align: start;
	}
}

:root {
	/* Colors */
	--col_back_base: #f2f2f2;
	--col_back_elevated: rgba(250, 250, 250, 0.7);
	--col_back_ele_opaque: rgba(250, 250, 250, 1);
	--col_active: #1a42f4;
	--col_active_2: #4162f6;
	--col_active_lite: #4464f4;
	--col_active_shadow: rgba(26, 66, 244, 0.3);
	--col_active_shadow_lite: rgba(26, 66, 244, 0.16);
	--col_active_2_shadow: rgba(65, 98, 246, 0.3);
	--col_active_text: var(--col_back_base);
	--col_action: #fff176;
	--col_action_2: #ffee5c;
	--col_action_shadow: rgba(255, 241, 118, 0.16);
	--col_action_2_shadow: rgba(255, 238, 92, 0.16);
	--col_action_text: var(--col_text_head);
	--col_text_head: rgb(50, 50, 50);
	--col_text_code: rgb(16, 32, 40);
	--col_text_sub: rgba(16, 32, 40, 0.8);
	--col_text_quote: rgba(40, 40, 40, 0.75);

	--size_text_head_1_lap: 5rem;
	--size_text_head_1_mob: 3rem;
	--size_text_head_2_lap: 4rem;
	--size_text_head_2_mob: 2rem;
	--size_text_sub_lap: 2rem;
	--size_text_sub_mob: 1.2rem;
	--size_text_code_lap: 1rem;
	--size_text_code_mob: 0.8rem;

