.emphasize_bi {
	font-weight: 600;
	font-style: italic;
}

.b {
	font-weight: 600;
}

.sb {
	font-weight: 500;
}

.i {
	font-style: italic;
}

.smlcaps {
	font-feature-settings: "c2sc";
	font-variant: small-caps;
}

.active_text {
	color: #1a42f4;
	color: var(--col_active);
}

.app {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	/* background-color: #282c34; */
	/* background-color: var(--col_back_base); */
	text-align: center;
	font-size: calc(10px + 2vmin);
	text-rendering: optimizelegibility;
}

.app_logo {
	height: 92%;
	align-self: center;
	justify-self: center;
}

.app_header {
	height: 80px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	display: sticky;
	position: flex-start;
	align-items: center;
	padding: 1vh 4vh;
	box-shadow: 0 3px 6px rgba(34, 34, 34, 0.16);
	z-index: 10;
}

.app_container {
	min-height: 80vh;
	width: 100%;
	position: relative;
}

.app_back_animated {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;

	width: 100%;
	max-height: unset;
	/* height: calc(200%); */
	z-index: -1;
	background-color: #f2f2f2;
	background-color: var(--col_back_base);
	color: #1a42f4;
	color: var(--col_active);
}

.app_title {
	color: rgb(16, 32, 40);
	color: var(--col_text_code);
	font-family: 'Poppins';
	font-size: 1rem;
	text-align: start;
	line-height: 1.1em;
	font-weight: 500;
	letter-spacing: 2px;
	font-feature-settings: "smcp", "c2sc";
	font-variant-caps: all-small-caps;
}

.btn_cta {
	padding: 0.5rem 1rem;
	background: linear-gradient(60deg, #fff176, #ffee5c);
	background: linear-gradient(60deg, var(--col_action), var(--col_action_2));
	border-radius: 4px;
	/* background-color: var(--col_back_elevated);
	color: var(--col_active); */
	/* border-left: solid 2px var(--col_active); */
	/* border-radius: 2rem; */
	box-shadow: 0 1px 2px rgba(255, 238, 92, 0.16);
	box-shadow: 0 1px 2px var(--col_action_2_shadow);
	font-family: 'Poppins';
	font-size: 0.8rem;
	font-weight: 600;
	letter-spacing: 1.2px;
}

@media only screen and (min-width: 481px) {
	.btn_cta {
		background-color: rgba(250, 250, 250, 0.7);
		background-color: var(--col_back_elevated);
		padding: 0.5rem 1rem;
		font-size: 0.8rem;
		letter-spacing: 1.2px;
	}
}

